html, body {
  margin: 0;
  padding: 0;
  font-family: arial, sans-serif;
  font-weight: 100;
}

header {
  background: #121212;
  position: fixed;
  width: 100%;
  padding: 5px 0;
  text-align: center;
} 

header .trm-logo {
  width: 50px;
  vertical-align: middle;
  margin-right: 5px;
}

header .iswim-logo {
  width: 45px;
  vertical-align: middle;
  margin-right: 10px;
}

header p {
  display: inline-block;
  color: #fff;
  font-weight: 700;
  letter-spacing: 0.2px;
}

@media screen and (max-width: 600px) {
  header .trm-logo {
    width: 32px;
  }

  header .iswim-logo {
    width: 30px;
  }

  header p {
    letter-spacing: none;
    font-size: 14px;
    margin-top: px;
  }
}

.footer {
  font-size: 9px;
  text-align: center;
  padding: 50px;
  color: #999;
}

.main {
  padding: 70px 20px 0 20px;
  min-height: 80vh;
  max-width: 1050px;
  margin: 0 auto;
}

.picker select {
  border-color: #ececec;
  padding: 6px 6px;
  font-size: 16px;
  margin-left: 5px;

}

.g-chart {
  margin: 25px auto;
  position: relative;
}

.g-chart svg {
  overflow: visible;
  stroke-linecap: round;
  stroke-linejoin: round;
}

.g-chart path {
  fill: none;
}

.g-chart text {
  font-size: 12px;
}

.g-chart path.domain {
  display: none;
}

.g-year-ticks text, .x.axis text {
  font-size: 9px;
}

.x.axis line {
  stroke: #d3d3d3;
}

.x.axis .g-highlight {  
  display: block;
  font-weight: 700;
}

