.y.axis .tick:nth-child(2) {
  display: none;
}

.y.axis.left .tick text {
  fill: teal;
}

.y.axis.left .tick line {
  stroke: teal !important;
  opacity: 0.6;
}

.y.axis.right line {
  stroke: #000 !important;
  opacity: 0.3;
}

.y.axis.right text {
  fill: #000;
  opacity: 0.8;
}

.g-slc-fill {
  fill: teal;
}

.g-slc-fill-priv {
  fill: orange;
}

.g-slc-fill-cmp {
  fill: blue;
}

.g-cum-stroke {
  stroke: #000;
}

.g-line.g-cum-stroke {
  stroke-width: 1.5;
}

.g-doctor-label path {
  stroke: #d3d3d3;
  stroke-width: 0.5;
  fill: rgba(0,0,0,0.02);
}

.g-labels {
  display: none;
}

.g-labels.g-active, .g-labels.g-active-always {
  display: block;
}

.g-label-cont {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}

.g-abs-label {
  position: absolute;
  font-size: 11px;
  line-height: 1.1;
  font-weight: 100;
  text-align: center;
  z-index: 1000;
  overflow: visible;
}

.g-bold {
  font-weight: 700;
}

.g-label-a {
  transform: translate(-45%, 0);
  background: rgba(0,0,0,1);
  color: rgba(255,255,255,0.9);
  padding: 3px;
  border-radius: 3px;
  white-space: nowrap;
}

.g-label-a .g-slc-total {
  color: rgba(0,204,204,0.8);
}

/*.g-label-last.g-cumulative-sl {
  text-align: left;
}

.g-label-max.g-slc-total {
  text-align: right;
}*/

.g-abs-label .g-inner:first-child {
  font-weight: 700;
}

.g-abs-label .g-text-bg {
  background: #fff;
  padding: 0 2px;
  display: inline;
  white-space: nowrap;
}

.g-abs-label .g-pointer {
  width: 1px;
  height: 8px;
  background: #000;
  position: absolute;
  bottom: 0;
}

.g-abs-label .g-dot {
  width: 5px;
  height: 5px;
  background: #000;
  position: absolute;
  border-radius: 100%;
  bottom: -2.5px;
  border: 1px solid #fff;
}

.g-slc-total {
  color: teal;
}

.g-action-log-cont {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.g-action-log {
  position: absolute;
  user-select: none;
  cursor: pointer;
  font-size: 12px;
}

.g-action-log-dot {
  width: 6px;
  height: 6px;
  background: rgba(0,0,0,1);
  position: absolute;
  border-radius: 100%;
  border: 1px solid #fff;
}

.g-action-log-line {
  position: absolute;
  top: 25px;
  left: 0;
  border-right: 1px dashed rgba(0,0,0,0.2);
}

.g-action-log-inner {
  transform: translate(-50%,0);
  background: rgba(0,0,0,0.7);
  color: #fff;
  padding: 3px 5px;
  border-radius: 3px;
  text-align: center;
  position: relative;
}

.g-action-highlighted {
  margin-top: -12px;
  z-index: 20;
}

.g-action-log-inner:after {
    position: absolute;
    top: 100%;
    height: 0;
    width: 0;
    left: calc(50% - 6px);
    border: 6px solid transparent;
    border-top-color: rgba(0,0,0,0.7);
    content: "";
}

.g-benchmark-cont {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.g-benchmark-line {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}

.g-benchmark-line:before {
  content: "";
  height: 100%;
  width: 2px;
  background: orange;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.g-benchmark-text {
  background: orange;
  color: #fff;
  padding: 3px;
  display: inline-block;
  font-size: 12px;
  position: absolute;
  top: 0;
  left: 0;
}